export default {
	data() {
		return {
			myindex: 0, //0 企业 1 个人
			type: 0,    // 认证状态
			shopImg:''
		};
	},
	methods: {
		// 初始化数据
		myajax:function(){
			var that=this
			uni.showLoading({
			    title: '加载中',
				mask:true
			});
			uni.request({
			    url: 'https://www.example.com/request', //仅为示例，并非真实接口地址。
			    data: {
			        text: 'uni.request'
			    },
			    success: (res) => {
			        console.log(res.data);
					that.mydata=res.data
			    }
			});
			setTimeout(function() {
				uni.hideLoading()
			}, 1000);
		},
		// 切换认证方式
		bian: function(index) {
			this.myindex = index;
		},
		// 打开新页面
		opennew:function(id){
			uni.navigateTo({
				url: '../'+id+'/'+id
			});
			
		},
		// 复制网址
		copyUrl:function(){
			uni.setClipboardData({
			    data: 'www.jiuwanjiuqian.com',
			    success: function () {
			        
			    }
			});
		},
		// 重新认证
		reset:function(){
			this.type=0
		},
		// 上传图片
		choseimage: function(index) {
			var that = this;
			//调取相册
			uni.chooseImage({
				count: 1, //默认9
				success: function(res) {
					uni.uploadFile({
						url: getApp().globalData.byurl + '/buyer/base/upload', //仅为示例，非真实的接口地址
						filePath: res.tempFilePaths[0],
						name: 'file',
						formData: {
							scene: 'idcard'
						},
						success: uploadFileRes => {
							that.shopImg  = JSON.parse(uploadFileRes.data).url;
						},
						fail: err => {
							console.log(err);
						}
					});
				},
				fail: function(fa) {
					//提示框
					uni.showToast({
						title: '失败，请重试',
						duration: 2000
					});
				}
			});
		}
	},
	onShow:function(){
		this.myajax()
	}
};
